$bg: #212121;
$active: #ffc740;
$font-color: #eee;

.SideDrawer {
  position: fixed;
  width: 224px;
  max-width: 70%;
  height: 100%;
  right: 0;
  top: 0;
  z-index: 200;
  background-color: $bg;
  padding: 32px 0;
  box-sizing: border-box;
  border-left: 2px solid #424242;
  transition: transform 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.01) 300ms;

  nav {
    margin-top: 64px;
    height: 100%;
    background-color: transparent !important;
    box-shadow: none !important;

    a {
      position: relative;
      width: 222px;
      text-align: right;
      margin: 0;
    }
  }

  .Back {
    margin: 1rem;
    padding: 0.5rem;
    position: fixed;
    right: 0;
    top: 0;
    width: 3.2rem;
    height: 3.2rem;
    box-sizing: border-box;
    cursor: pointer;
    border-radius: 50%;
    border: 1px solid #4242423a;
    transition: all .66 cubic-bezier(0.165, 0.84, 0.44, 1);
    background-color: $active;

    &:hover,
    &:active {
      transform: rotateZ(-360deg);
      box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),
            0 3px 1px -2px rgba(0,0,0,0.12),
            0 1px 5px 0 rgba(0,0,0,0.2) !important;
    }
  }

  @media (min-width: 500px) {
    display: none;
  }
}

.Open {
  transform: translateX(0);
}

.Close {
  transform: translateX(120%);
}